import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/views/Main'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: Main,
      children: [
        {
          path: '/bank',
          name: '银行管理',
          component: () => import('@/views/bank')
        },
        {
          path: '/user',
          name: '用户管理',
          component: () => import('@/views/user')
        },
        {
          path: '/transform',
          name: '转账操作',
          component: () => import('@/views/transform')
        },
        {
          path: '/balance',
          name: '余额查询',
          component: () => import('@/views/balance')
        }
      ]
    }
  ]
})



