<template>
    <el-container class="index-container">
        <el-header id='header'>
            <el-menu class="el-menu-demo" mode="horizontal" text-color="black">
              <el-menu-item style="font-size:20px;"><router-link to='/' class="a">同态加密跨行转账</router-link></el-menu-item>
              <el-menu-item style="float:right;">
                  <template slot="title">欢迎您</template>
              </el-menu-item>
            </el-menu>
        </el-header>
        <el-container id="content">
            <el-aside width="200px">
               <asideBar></asideBar>
            </el-aside>
            <el-main>
                <el-breadcrumb separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item v-for="item in levelList" :key="item.path">
                    {{item.name}}
                  </el-breadcrumb-item>
                </el-breadcrumb>
                <Content></Content>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import headerBar from '../components/common/bar/headerBar'
import asideBar from '../components/common/bar/asideBar'
import Content from './Content'

export default {
  name: 'Main',
  components: {
    headerBar,
    asideBar,
    Content
  },
  data () {
    return {
      levelList: null,
      username: ''
    }
  },
  watch: {
    $route () {
      this.getBreadcrumb()
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.getBreadcrumb()
      this.username = localStorage.getItem('username')
      this.username = '费朝晖'
    },
    getBreadcrumb () {
      let matched = this.$route.matched.filter(item => item.name)
      this.levelList = matched
    },
    logout () {
    }
  }
}
</script>

<style scoped>
body {
    height: 100%;
}
#header {
    /* margin-bottom: 10px; */
    padding: 0;
    /* background-color: #545c64; */
    color: white;
}
#content {
    flex: 1;
}
.el-aside {
    /* background-color: #1a1b20 !important; */
    /* color: #1a1b20 !important; */
}
.el-main {
    background-color: #E9EEF3;
    color: #1a1b20 !important;
    text-align: center;
}
.el-menu.el-menu–inline {
    overflow: auto;
}
body > .el-container {
    margin-bottom: 40px;
}
.index-container {
    height: 100%;
}
.a {
  text-decoration: none;
}
.el-menu {
  /* background-color: #1a1b20 !important; */
}
.el-submenu{
  /* background-color: #1a1b20 !important; */
}
.el-menu-item{
  /* background-color: #1a1b20 !important; */
}

</style>



